\n.ui-snackbar {\n  @include padding($space-slab $space-medium);\n  @include shadow;\n  background-color: $woot-snackbar-bg;\n  border-radius: $space-smaller;\n  display: inline-flex;\n  margin-bottom: $space-small;\n  max-width: 40rem;\n  min-height: 3rem;\n  min-width: 24rem;\n  text-align: left;\n}\n\n.ui-snackbar-text {\n  color: $color-white;\n  font-size: $font-size-small;\n  font-weight: $font-weight-medium;\n}\n\n.ui-snackbar-action {\n  margin-left: auto;\n  padding-left: 3rem;\n\n  button {\n    @include margin(0);\n    @include padding(0);\n    background: none;\n    border: 0;\n    color: $woot-snackbar-button;\n    font-size: $font-size-small;\n    text-transform: uppercase;\n  }\n}\n",".loading-state {\n  padding: $space-jumbo $space-smaller;\n\n  .message {\n    color: $color-gray;\n    display: block;\n    text-align: center;\n    width: 100%;\n  }\n\n  .spinner {\n    float: none;\n    top: -$space-smaller;\n  }\n}\n\n// EMPTY STATES\n.empty-state {\n  padding: $space-jumbo $space-smaller;\n\n  .title,\n  .message {\n    display: block;\n    text-align: center;\n    width: 100%;\n  }\n\n  .title {\n    font-size: $font-size-giga;\n    font-weight: $font-weight-feather;\n  }\n\n  .message {\n    color: $color-gray;\n    margin: $space-normal auto;\n    width: 90%;\n  }\n\n  .button {\n    margin-top: $space-medium;\n  }\n}\n",".status-bar {\n  @include flex;\n  flex-direction: column;\n  @include flex-align($x: center, $y: middle);\n  background: lighten($warning-color, 36%);\n  // @include elegant-card();\n  @include margin($zero);\n  @include padding($space-normal $space-smaller);\n\n  .message {\n    font-weight: $font-weight-medium;\n    margin-bottom: $zero;\n  }\n\n  .button {\n    @include margin($space-smaller $zero $zero);\n    padding: $space-small $space-normal;\n  }\n\n  &.danger {\n    background: lighten($alert-color, 30%);\n\n    .button {\n      // Default and disabled states\n      &,\n      &.disabled, &[disabled],\n      &.disabled