//\n/// @argument {string} $position\n///   A CSS position value.\n///\n/// @argument {list} $box-edge-values\n///   List of lengths; accepts CSS shorthand.\n///\n/// @example scss\n///   .element {\n///     @include position(relative, 0 null null 10em);\n///   }\n///\n///   // CSS Output\n///   .element {\n///     left: 10em;\n///     position: relative;\n///     top: 0;\n///   }\n///\n/// @example scss\n///   .element {\n///     @include position(absolute, 0);\n///   }\n///\n///   // CSS Output\n///   .element {\n///     position: absolute;\n///     top: 0;\n///     right: 0;\n///     bottom: 0;\n///     left: 0;\n///   }\n///\n/// @require {function} _is-length\n///\n/// @require {function} _unpack-shorthand\n\n@mixin position(\n  $position,\n  $box-edge-values\n) {\n  $box-edge-values: _unpack-shorthand($box-edge-values);\n  $offsets: (\n    top:    nth($box-edge-values, 1),\n    right:  nth($box-edge-values, 2),\n    bottom: nth($box-edge-values, 3),\n    left:   nth($box-edge-values, 4),\n  );\n\n  position: $position;\n\n  @each $offset, $value in $offsets {\n    @if _is-length($value) {\n      #{$offset}: $value;\n    }\n  }\n}\n","table {\n  border-spacing: 0;\n  font-size: $font-size-small;\n\n  thead {\n    th {\n      font-weight: $font-weight-bold;\n      text-transform: uppercase;\n    }\n  }\n\n  tbody {\n    tr {\n      border-bottom: 1px solid $color-border-light;\n    }\n\n    td {\n      @include padding($space-one $space-small);\n    }\n  }\n}\n\n.woot-table {\n  tr {\n    .show-if-hover {\n      opacity: 0;\n      transition: opacity 0.2s $swift-ease-out-function;\n    }\n\n    &:hover {\n      .show-if-hover {\n        opacity: 1;\n      }\n    }\n  }\n\n  .agent-name {\n    display: block;\n    font-weight: $font-weight-medium;\n    text-transform: capitalize;\n  }\n\n  .woot-thumbnail {\n    border-radius: 50%;\n    height: 5rem;\n    width: 5rem;\n  }\n\n  .button-wrapper {\n    @include flex-align(left, null);\n    @include flex;\n    flex-